import React from 'react';
import { useParams } from 'react-router-dom';
import { LoadingCircle, RetryAgain } from '../../../widget/Loading';
import { Link } from 'react-router-dom';

import { Pipeline } from "./pipeline"
import { Tasks } from "./tasks"
import { Recommendation } from "./recommendation"
import { Recruiters } from "./recruiters"
import { Comments } from "./comments"
import { useFetchData } from '../../../widget/useFetchData';


export const OpeningDashboard = () => {
    const { id } = useParams();

    const URL = `agent/openings/dashboard/get-job-opening-profile/${id}/`
    const {error, isLoaded, data, setData, fetchData} = useFetchData(URL)

    return (
        <>
            {
                error !== null ?
                    <RetryAgain func={fetchData} error={error} />
                    : !isLoaded ? <LoadingCircle /> :
                        <>
                    <div >
                        <div>
                            <div className="clearfix ms-3 mt-3 me-3">
                                <div className="float-end">
                                    <div className="">
                                        <nav aria-label="breadcrumb">
                                            <ol className="breadcrumb">
                                                <li className="breadcrumb-item"><Link to="/company/openings">Openings</Link></li>
                                                <li className="breadcrumb-item active" aria-current="page">Job Preview</li>
                                            </ol>
                                        </nav>
                                    </div>
                                    
                                </div>
                                <div className="float-start">
                                    <h5>{data.job_title}</h5>
                                </div>
                            </div>
                            <div style={{borderBottom: "1px solid #eaeaea"}}>
                                <ul className="nav nav-line nav-light nav-tabs">
                                    <li className="nav-item">
                                        <a className="nav-link active" data-bs-toggle="tab" href="#pipline">
                                            <span className="nav-link-text">Pipeline</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" data-bs-toggle="tab" href="#recommendation">
                                            <span className="nav-link-text">Recommendation</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" data-bs-toggle="tab" href="#recruiters">
                                            <span className="nav-link-text">Recruiters</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" data-bs-toggle="tab" href="#comments">
                                            <span className="nav-link-text">Comments</span>
                                        </a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link" data-bs-toggle="tab" href="#tasks">
                                            <span className="nav-link-text">Tasks</span>
                                        </a>
                                    </li>
                                </ul>
                        
                            </div>													
                            <div className="tab-content">
                                <div className="tab-pane fade show active" id="pipline">
                                    <Pipeline />
                                </div>
                                <div className="tab-pane fade" id="recommendation">
                                    <Recommendation data={data} setData={setData} />
                                </div>
                                <div className="tab-pane fade" id="recruiters">
                                    <Recruiters data={data} setData={setData} />
                                </div>
                                <div className="tab-pane fade" id="comments">
                                    <Comments data={data} setData={setData} />
                                </div>
                                <div className="tab-pane fade" id="tasks">
                                    <Tasks data={data} setData={setData} />
                                </div>
                            </div> 
                                                            
                        </div>
                    </div>
                </>
            }
        </>
    )
}