import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import React from 'react';
import { Link, useParams } from 'react-router-dom';
import { LoadingCircle, RetryAgain } from '../../../widget/Loading';
import { BASE_MEDIA_URL } from '../../../authentication/api';
import { useFetchData } from '../../../widget/useFetchData';


export const Pipeline = (props) =>{
    const { id } = useParams();
    const URL = `agent/openings/dashboard/get-all-pipline-candidates-list/${id}/`
    const {error, isLoaded, data, setData, fetchData} = useFetchData(URL);

    return (
        <>
            {
                error !== null ?
                    <RetryAgain func={fetchData} error={error} />
                    : !isLoaded ? <LoadingCircle /> :
                        <>
                        <MyApp data={data} setData={setData} />
                    </>
            }
        </>
    )
}

// openings/dashboard/get-all-pipline-candidates-list/

function MyApp(props) {

    const  onDragEnd = result => {
    const { destination, source, draggableId } = result;

    if (!destination) {
      return;
    }

    if (
      destination.droppableId === source.droppableId &&
      destination.index === source.index
    ) {
      return;
    }

    const start = props.data.columns[source.droppableId];
    const finish = props.data.columns[destination.droppableId];

    if (start === finish) {
      const newTaskIds = Array.from(start.taskIds);
      newTaskIds.splice(source.index, 1);
      newTaskIds.splice(destination.index, 0, draggableId);

      const newColumn = {
        ...start,
        taskIds: newTaskIds,
      };

      const newState = {
        ...props.data,
        columns: {
          ...props.data.columns,
          [newColumn.id]: newColumn,
        },
      };
      props.setData(newState);
      return;
    }

    // Moving from one list to another
    const startTaskIds = Array.from(start.taskIds);
    startTaskIds.splice(source.index, 1);
    const newStart = {
      ...start,
      taskIds: startTaskIds,
    };

    const finishTaskIds = Array.from(finish.taskIds);
    finishTaskIds.splice(destination.index, 0, draggableId);
    const newFinish = {
      ...finish,
      taskIds: finishTaskIds,
    };

    const newState = {
      ...props.data,
      columns: {
        ...props.data.columns,
        [newStart.id]: newStart,
        [newFinish.id]: newFinish,
      },
    };
    props.setData(newState);
  };
    
  return (
    <DragDropContext  onDragEnd={onDragEnd}>
        {/* <div > */}
            <ListComponents data={props.data} />
        {/* </div> */}
    </DragDropContext>
  );
}


export function ListComponents(props) {
    
  const [ enabled, setEnabled ] = React.useState(false);

  React.useEffect(() => {
    const animation = requestAnimationFrame(() => setEnabled(true));

    return () => {
       cancelAnimationFrame(animation);
       setEnabled(false);
    };
  }, []);

  if (!enabled) {
      return null;
  }
    return (
        <div className='d-flex flex-row flex-nowrap overflow-auto my-cards-container pb-0'>
            {props.data.columnOrder.map((columnId, index) => {
                const column = props.data.columns[columnId];
                const tasks = column.taskIds.map((taskId) => props.data.task[taskId]);
                const noTasks = tasks.length;
                return (
                    <div className='m-1 mb-0 pb-0' key={`droppable-container-${index}`}  style={{minWidth: "250px"}}>
                        <div className="card card-border">
                            <div className="card-header bg-light p-2">
                                <div>
                                    <h6 className="text-uppercase fw-bold  d-flex align-items-center mb-0"><span className="tasklist-name">{column.title}</span><span className="badge badge-pill badge-soft-violet ms-2">{noTasks}</span></h6>
                                </div>
                                <div>
                                    <Link className="btn btn-icon btn-rounded btn-flush-dark flush-soft-hover dropdown-toggle no-caret" data-bs-toggle="dropdown" aria-expanded="false"><span className="icon"><span className="feather-icon"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" className="feather feather-more-vertical"><circle cx="12" cy="12" r="1"></circle><circle cx="12" cy="5" r="1"></circle><circle cx="12" cy="19" r="1"></circle></svg></span></span></Link>
                                    <div className="dropdown-menu dropdown-menu-end">
                                        <a className="dropdown-item" href="#--">Action</a>
                                        <a className="dropdown-item" href="#--">Another action</a>
                                        <a className="dropdown-item" href="#--">Something else here</a>
                                        <div className="dropdown-divider"></div>
                                        <a className="dropdown-item" href="#--">Separated link</a>
                                    </div>
                                </div>
                            </div>
                            <div className="card-body p-0 m-0 bg-light">
                                <div>
                                    <Droppable droppableId={columnId} key={`droppable-${index}`} >
                                        {(provided, snapshot) => (
                                            <div
                                                {...provided.droppableProps}
                                                ref={provided.innerRef}
                                            >
                                                <div className="p-1 m-0 my-cards-container" style={{height:"420px", overflowY: "scroll"}}>
                                                    {tasks.map((data, index) => (
                                                        <Draggable key={data.id} draggableId={data.id} index={index}>
                                                            {(provided, snapshot) => (
                                                                <div
                                                                    key={index}
                                                                    ref={provided.innerRef}
                                                                    {...provided.draggableProps}
                                                                    {...provided.dragHandleProps}
                                                                >
                                                                <div className="card card-border m-0" style={{borderRadius:"0px"}}>
                                                                    <div className="card-body p-2">
                                                                        <div className="media align-items-center">
                                                                            <div className="media-head me-2">
                                                                                <div className="avatar avatar-sm avatar-rounded position-relative">
                                                                                    <img src={`${BASE_MEDIA_URL}${data.applicant.avatar}`} alt={data.applicant.first_name} className="avatar-img" />
                                                                                </div>
                                                                            </div>
                                                                            <div className="media-body">
                                                                                <Link to={`/company/candidates/single-candidate/${data.id}`}>
                                                                                    <div className="text-high-em" style={{fontSize:"12px", fontWeight:"bolder"}}>{data.applicant.first_name} {data.applicant.last_name}</div> 
                                                                                    <p className="text-high-em" style={{fontSize:"10px"}}>{data.applicant.email}</p> 
                                                                                </Link>
                                                                            </div>

                                                                            <div className="media-head ms-2">
                                                                                <Link className="dropdown-toggle no-caret" data-bs-toggle="dropdown" aria-expanded="false">
                                                                                <i className="bi bi-three-dots" style={{color:"black"}}></i>
                                                                                </Link>
                                                                                <div className="dropdown-menu dropdown-menu-end">
                                                                                    <a className="dropdown-item" href="#--">Action</a>
                                                                                    <a className="dropdown-item" href="#--">Another action</a>
                                                                                    <a className="dropdown-item" href="#--">Something else here</a>
                                                                                    <div className="dropdown-divider"></div>
                                                                                    <a className="dropdown-item" href="#--">Separated link</a>
                                                                                </div>
                                                                            </div>
                                                                        </div>
                                                                        <div className="separator m-0 p-0 mt-2 mb-2"></div>
                                                                        <div className="clearfix">
                                                                            <div className="float-start">
                                                                                <span className="badge badge-warning  badge-sm">
                                                                                <span> <span className="icon"><i className="ri-star-s-fill"></i></span>3.5</span>
                                                                                </span>
                                                                            </div>
                                                                            <div className="float-end">
                                                                                    <i className="bi bi-people"></i>
                                                                            </div>
                                                                        </div>
                                                                    </div>  
                                                                </div>
                                                                
                                                            </div>
                                                            )}
                                                        </Draggable>
                                                    ))}
                                                </div>
                                                {/* {provided.placeholder} */}
                                            </div>
                                        )}
                                    </Droppable> 
                                </div>
                            </div>
                        </div>
                    </div>
                )
            })}
        </div>
    )

}