import { CandidateDashboard } from "./candidates/dashboard"
import { JobSubmission } from "./candidates/submission"
import { useAuthContext } from "../authentication/useAuthContext";
import { Navigate } from "react-router-dom";
import { LOGINPATH } from "../authentication/api";


export const Candidate = () => {
    const { user } = useAuthContext();
    
    if (!user) {
        return <Navigate replace to={LOGINPATH} />;
    }
    return (
        <>
            <div style={{borderBottom: "1px solid #eaeaea"}}>
                <ul className="nav nav-line nav-light nav-tabs">
                    <li className="nav-item">
                        <a className="nav-link active" data-bs-toggle="tab" href="#submission">
                            <span className="nav-link-text">Job Submission</span>
                        </a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" data-bs-toggle="tab" href="#candidates">
                            <span className="nav-link-text">Candidates</span>
                        </a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" data-bs-toggle="tab" href="#folder">
                            <span className="nav-link-text">Folder</span>
                        </a>
                    </li>
                </ul>
            
            </div>													
            <div className="tab-content">
                <div className="tab-pane fade show active" id="submission">
                    <JobSubmission />
                </div>
                <div className="tab-pane fade" id="candidates">
                    <CandidateDashboard />
                </div>
                <div className="tab-pane fade" id="folder">
                    <CandidateDashboard />
                </div>
            </div> 
        </>
    )
}