import { Link } from "react-router-dom"
import { BASE_MEDIA_URL } from "../../authentication/api";
import { useFetchData } from "../../widget/useFetchData";
import DataTableBase from "../../widget/dataTable";
import { RetryAgain, LoadingCircle } from "../../widget/Loading";
import { dateFormatter } from "../../funcs/formatDate";


const columns = [
  {
    name: "Full Name",
	selector: (row) => row.first_name,
	minWidth:"200px",
    sortable: true,
    cell: (row)=>{
		return (
			<div className="mt-3 mb-3">
				<div className="media align-items-center">
					<div className="media-head me-2">
						<div className="avatar avatar-xs avatar-rounded">
							<img src={`${BASE_MEDIA_URL}${row.avatar}`} alt={row.full_name} className="avatar-img" />
						</div>
					</div>
					<div className="media-body">
						<div className="text-high-em">{row.first_name} {row.last_name}</div> 
						<p className="fs-7">{row.email}</p> 
					</div>
				</div>
			</div>
	  )
    }
  },
  {
		name: "Phone Number",
		selector: (row) => row.phone_number,
        sortable: true,
		cell: (row) => (
			<p className="fs-7">{ row.phone_number}</p>
		)
  },
  {
		name: "Role",
		selector: (row) => row.role,
		sortable: true,
	    left: true,
		cell: (row) => (
			<p className="fs-7">{ row.role}</p>
		)
  },

  {
		name: "Created Date",
		selector: (row) => row.created_at,
		sortable: true,
	    right: true,
		cell: (row) => (
			<p className="fs-7">{ dateFormatter(row.created_at)}</p>
		)
  },
];

export const UserManagement = () => {
	const URL = `agent/settings/user-management/get-all-users-list/`
	const {error, isLoaded, data, fetchData } = useFetchData(URL)

    return (
        <>
            <div className="m-3">
                <div className="clearfix mb-3">
                    <div className="float-end mb-3">
                        <div className="">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb">
                                    <li className="breadcrumb-item"><Link to="/company/settings">Settings</Link></li>
                                    <li className="breadcrumb-item active" aria-current="page">User Management</li>
                                </ol>
                            </nav>
                        </div>
                        
                    </div>
                    <div className="float-start">
                        <h5>User Management</h5>
                    </div>
                </div>
					{
						error !== null ?
							<RetryAgain func={fetchData} error={error} />
							: !isLoaded?<LoadingCircle />:
						
                            <div className="m-3">
                                <div className="d-flex flex-md-nowrap flex-wrap align-items-center justify-content-between mb-2">
                                    <div>
                                        {/* <h5>All Users</h5> */}
                                    </div>
                                    <div>
                                        <Link to="/company/settings/user-management/add-user" className="btn btn-primary btn-sm">Add User</Link>
                                    </div>   
                                </div>
                                <DataTableBase
                                    columns={columns}
                                    data={data}
                                />
                            </div>
					}
            </div>
        </>
    )
}
