import { useEffect, useState } from "react"
import instance from "../authentication/axiosInstance";


export const useFetchData = (url) => {
	const [error, setError] = useState(null);
  	const [isLoaded, setIsLoaded] = useState(false);
	const [data, setData] = useState(null);
    const URL = url
	const fetchData1 = (urlLink) => {
		// for retry
		setError(null)
		instance({
			url: urlLink,
			method: "GET",
			}).then((res) => {
				// handle success
				setIsLoaded(true)
				setData(res.data)
				console.log(res.data);
			}).catch((e)=>{
				console.log(e)
				setError(e)
			});
	};
    useEffect(() => {
       fetchData1(URL)
    }, [URL]);
    const fetchData = () =>fetchData1(URL)
    return {
        error, setError, isLoaded, setIsLoaded, data, setData, fetchData,
    }
}