import React from 'react';
import { useParams } from 'react-router-dom';
import { LoadingCircle, RetryAgain } from '../../../widget/Loading';
import { BASE_MEDIA_URL } from '../../../authentication/api';
import { useFetchData } from '../../../widget/useFetchData';


export const Recommendation = () =>{
    const { id } = useParams();
    const URL = `agent/openings/dashboard/get-all-recommended-applicants/${id}/`
    const {error, isLoaded, data, fetchData } = useFetchData(URL)

    return (
        <>
            <div className="m-3">
                {
                    error !== null ?
                        <RetryAgain func={fetchData} error={error} />
                        : !isLoaded ? <LoadingCircle /> :
                            <>
                                <div>
                                    <table className="table">
                                        <thead>
                                            <tr>
                                            <th scope="col"></th>
                                            <th scope="col"></th>
                                            <th scope="col"></th>
                                            <th scope="col"></th>
                                            <th scope="col"></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {data.map((item, ind) => {
                                                return (
                                                    <tr key={ind}>
                                                        <th scope="row">
                                                            <div className="avatar avatar-rounded position-relative avatar-sm">
                                                                <img src={`${BASE_MEDIA_URL}${item.avatar}`} alt="user" className="avatar-img" />
                                                            </div>               
                                                        </th>
                                                        <td>
                                                            {item.first_name} {item.last_name}
                                                        </td>
                                                        <td>
                                                            <ul>
                                                                <li><b>Experience</b></li>
                                                                {item.work.map(dx => (<li key={dx.id}>{dx.position} <span> 2 years</span></li>))}
                                                                
                                                            </ul>
                                                        </td>
                                                        <td>
                                                            <div className="">
                                                                {item.skills.map((di) => (
                                                                    <span key={di.id} className="badge badge-soft-primary m-1">{ di.skill}</span>
                                                                ))}
                                                                {item.language.map((di) => (
                                                                    <span key={di.id} className="badge badge-soft-secondary m-1">{ di.my_language}</span>
                                                                ))}
                                                            </div>
                                                        </td>
                                                        <td>
                                                            <button className="btn btn-primary btn-sm">Add to pipline</button>
                                                        </td>
                                                    </tr>    
                                                )
                                            })}
                                        </tbody>
                                    </table>
                                </div>
                            </>
                }
            </div>
        </>
    )
}