import { toggleButton } from "../funcs/toggle"
import { Link } from "react-router-dom"
import { useFetchData } from "../widget/useFetchData"
import { RetryIcon, LoadingCircle } from "../widget/Loading"
import { BASE_MEDIA_URL } from "../authentication/api"
import { searchQuery } from "./search"
import { LOGINPATH } from "../authentication/api"
import { Navigate } from "react-router-dom"
import { useAuthContext } from "../authentication/useAuthContext"


export const NavBar = () => {
    return (
		<nav className="hk-navbar navbar navbar-expand-xl navbar-light fixed-top">
            <div className="container-fluid">
                
                <div className="nav-start-wrap">
                    <button className="btn btn-icon btn-rounded btn-flush-dark flush-soft-hover navbar-toggle d-xl-none" onClick={toggleButton}>
                        <span className="icon">
                            <span className="feather-icon">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-align-left">
                                    <line x1="17" y1="10" x2="3" y2="10"></line>
                                    <line x1="21" y1="6" x2="3" y2="6"></line>
                                    <line x1="21" y1="14" x2="3" y2="14"></line>
                                    <line x1="17" y1="18" x2="3" y2="18"></line>
                                </svg>
                            </span>
                        </span>
                    </button>
                        
                    <Searching />
                    </div>
                    
                <div className="nav-end-wrap">
                    <ul className="navbar-nav flex-row">
                       
                        <Notifications />
                        <ProfileAvatar />

                    </ul>
                </div>
			</div>									
		</nav>
    )
}

const Searching = (props) => {
    return (
        <>
            <form className="dropdown navbar-search">
                <div className="dropdown-toggle no-caret" data-bs-toggle="dropdown" data-dropdown-animation="" data-bs-auto-close="outside">
                    <Link className="btn btn-icon btn-rounded btn-flush-dark flush-soft-hover  d-xl-none"><span className="icon"><span className="feather-icon"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-search"><circle cx="11" cy="11" r="8"></circle><line x1="21" y1="21" x2="16.65" y2="16.65"></line></svg></span></span></Link>
                    <div className="input-group d-xl-flex d-none">
                        <span className="input-affix-wrapper input-search affix-border">
                            <input type="text" className="form-control  bg-transparent" data-navbar-search-close="false" placeholder="Search..." aria-label="Search" />
                            <span className="input-suffix"><span>/</span>
                                <span className="btn-input-clear"><i className="bi bi-x-circle-fill"></i></span>
                                <span className="spinner-border spinner-border-sm input-loader text-primary" role="status">
                                    <span className="sr-only">Loading...</span>
                                </span>
                            </span>
                        </span>
                    </div>
                </div>
                <div className="dropdown-menu p-0">
                    <div className="dropdown-body p-2">
                        {searchQuery.map((item, ind) => {
                            return (
                                <Link className="dropdown-item" key={ind} to={item.link}>
                                    <div className="media align-items-center">
                                        <div className="media-head me-2">
                                            <div className="avatar avatar-icon avatar-xs avatar-soft-light avatar-rounded">
                                                <span className="initial-wrap">
                                                    <span className="svg-icon">
                                                        <svg xmlns="http://www.w3.org/2000/svg" className="icon icon-tabler icon-tabler-corner-down-right" width="24" height="24" viewBox="0 0 24 24" strokeWidth="2" stroke="currentColor" fill="none" strokeLinecap="round" strokeLinejoin="round">
                                                            <path stroke="none" d="M0 0h24v24H0z" fill="none"></path>
                                                            <path d="M6 6v6a3 3 0 0 0 3 3h10l-4 -4m0 8l4 -4"></path>
                                                        </svg>
                                                    </span>
                                                </span>
                                            </div>
                                        </div>
                                        <div className="media-body">
                                            {item.name}
                                        </div>
                                    </div>
                                </Link>
                            )
                        })}
                    </div>
                </div>
            </form>
        </>
    )
}

const ProfileAvatar = (props) => {
    const { setUser } = useAuthContext();
    const URL = `agent/profile/get-individual-profile/`
    const {error, isLoaded, data, fetchData} = useFetchData(URL)
    const logout = () => {
        // console.log("Hello World")
        localStorage.clear();
        setUser(null)
        return <Navigate replace to={LOGINPATH} />;
    }
    return (
        <>
            <li className="nav-item">
                {
                    error !== null ?
                        <RetryIcon func={fetchData} error={error} />
                        : !isLoaded ? <LoadingCircle /> :
                            <>
                            <div className="dropdown ps-2">
                                <Link className=" dropdown-toggle no-caret" role="button" data-bs-display="static" data-bs-toggle="dropdown" data-dropdown-animation="" data-bs-auto-close="outside" aria-expanded="false">
                                    <div className="avatar avatar-rounded avatar-xs">
                                        <img src={`${BASE_MEDIA_URL}${data.avatar}`} alt={data.first_name} className="avatar-img" />
                                    </div>
                                </Link>
                                <div className="dropdown-menu dropdown-menu-end">
                                    <Link className="dropdown-item" to="/company/settings/profile/individual">Profile</Link>
                                    {/* <a className="dropdown-item" href="#..."><span className="me-2">Offers</span><span className="badge badge-sm badge-soft-pink">2</span></a><div className="dropdown-divider"></div> */}
                                    <button className="dropdown-item" onClick={logout}>Logout</button>
                                </div>
                            </div>
                    </>}
            </li>
        </>
    )
}

