import DataTableBase from '../../widget/dataTable';
import { Link } from "react-router-dom";
import { InviteCandidate } from "./inviteCandidate";
import { LoadingCircle, RetryAgain } from "../../widget/Loading";
import { BASE_MEDIA_URL } from "../../authentication/api";
import { useFetchData } from "../../widget/useFetchData";
import { useCallback, useMemo } from 'react';
import { findAverage } from '../../widget/functions';


const columns = [
  {
    name: "Name",
	minWidth: "170px",
    selector: (row) => row.applicant.first_name,
    sortable: true,
    cell: (row)=>{
		return (
			<div className="mt-3 mb-3">
				<Link to={`/company/candidates/single-candidate/${row.id}`}>
					<div className="media align-items-center">
						<div className="media-head me-2">
							<div className="avatar avatar-xs avatar-rounded">
								<img src={`${BASE_MEDIA_URL}${row.applicant.avatar}`} alt={row.applicant.first_name} className="avatar-img" />
							</div>
						</div>
						<div className="media-body">
							<div className="text-high-em">{row.applicant.first_name} {row.applicant.last_name}</div> 
							<p className="fs-7">{row.applicant.current_role}</p> 
						</div>
					</div>
				</Link>

			</div>
	  )
    }
  },
  {
    name: "Location",
    selector: (row) => row.applicant.city,
	sortable: true,
    cell: (row)=>{
		return (
			<div className="mt-3 mb-3">
				<div className="media align-items-center">
					<div className="media-body">
						<div className="text-high-em">{row.applicant.city}</div> 
						<p className="fs-7">{row.applicant.country}</p> 
					</div>
				</div>
			</div>
			)
		}
    },
    {
		name: "Current Company",
		selector: (row) => row.company,
		sortable: true,
		cell: (row) => (
			<p className="fs-7">row.company</p>
		)
    },
    {
		name: "Rating",
		width: "90px",
		selector: (row) => findAverage(row.rate.map(a=>a.rate)),
		sortable: true,
		cell: (row) => (
		<span className="badge badge-warning  badge-sm">
			<span><span className="icon"><i className="ri-star-s-fill"></i></span>
			{findAverage(row.rate.map(a=>a.rate))}
				</span>
		</span>
	)
    },
   {
		name: "Current Position",
		selector: (row) => row.position,
	    sortable: true,
		cell: (row) => (
			<p className="fs-7">row.position</p>
		)
	},
    {
		name: "Status",
		width: "130px",
		selector: (row) => row.pipline.name,
		sortable: true,
		cell: (row) => (
			<p className="fs-7">{ row.pipline.name}</p>
		)
  },
];

export const CandidateDashboard = () => {
	const URL = "agent/applications/get-all-candidates-list/"
	const { error, isLoaded, data, fetchData} = useFetchData(URL)

	const handleRowSelected = useCallback(state => {
		// setSelectedRows(state.selectedRows);
	}, []);

	const contextActions = useMemo(() => {
		return (
			<>
				<button className="btn btn-icon btn-rounded btn-flush-light flush-soft-hover "><span className="icon"><i className="bi bi-share"></i></span></button>
				<button className="btn btn-icon btn-rounded btn-flush-light flush-soft-hover "><span className="icon"><i className="bi bi-trash"></i></span></button>

				<button className="btn btn-icon btn-rounded btn-flush-dark flush-soft-hover dropdown-toggle no-caret" href="#" data-bs-toggle="dropdown"><span className="icon"><span className="feather-icon"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-more-vertical"><circle cx="12" cy="12" r="1"></circle><circle cx="12" cy="5" r="1"></circle><circle cx="12" cy="19" r="1"></circle></svg></span></span></button>
				<div className="dropdown-menu dropdown-menu-end">
					<Link className="dropdown-item">Action</Link>
					<Link className="dropdown-item">Another action</Link>
					<Link className="dropdown-item">Something else here</Link>
					<div className="dropdown-divider"></div>
					<Link className="dropdown-item">Separated link</Link>
				</div>
			</>
			
		);
	}, []);

	const actionsMemo = useMemo(() => {
		return (
			<>
                <input type="text" className="form-control" placeholder="Search Candidate..." style={{ width: "200px" }} />
				<InviteCandidate />
                <button className="btn btn-secondary">Advanced Autosourcing</button>
			</>
		)
	}, [])
    return (
        <>
            <div>
                <div className="mt-3 ms-5 me-5">			
                    <div>
					{
						error !== null ?
							<RetryAgain func={fetchData} error={error} />
							: !isLoaded?<LoadingCircle />:
						<>
							<div className="d-flex flex-md-nowrap flex-wrap align-items-center justify-content-between mb-2">
								<div>
									<h5>Candidates</h5>
									<p>Manage all candidates</p>
								</div>
							</div>
							<DataTableBase
								columns={columns}
								data={data}
                                actions={actionsMemo}
								contextActions={contextActions}
								onSelectedRowsChange={handleRowSelected}
								// clearSelectedRows={toggleCleared}
								selectableRows
							/>
						</>
					}
                    </div>
													
                </div>
            </div>
        </>
    )
}