
export const LoadingCircle = () => {
    return (
        <>
            <div>
                <div className="spinner-grow text-primary spinner-boarder-lg" role="status">
                    <span className="visually-hidden">Loading...</span>
                </div>
            </div>
        </>
    )
}

export const RetryAgain = (props) => {
    return (
        <>
            <div>
                {props.error.message} occured <button className="btn btn-link" onClick={props.func}>Retry</button>
            </div>
        </>
    )
}
export const RetryIcon = (props) => <button className="btn btn-link" onClick={props.func}>Retry</button>

