import { Link } from "react-router-dom"
import { LoadingCircle, RetryAgain } from "../../../widget/Loading";
import { useFetchData } from "../../../widget/useFetchData";


export const Jobs = () => {
    // openings/get-job-opening-list/
    const URL = `agent/openings/get-job-opening-list/`
    const {error, isLoaded, data, fetchData } = useFetchData(URL)

    return (
        <>
            <div>
                <div className="mt-3 ms-5 me-5">
                    <div>
					{
						error !== null ?
							<RetryAgain func={fetchData} error={error} />
							: !isLoaded?<LoadingCircle />:
						<>
                            <div className="d-flex flex-md-nowrap flex-wrap align-items-center justify-content-between mb-2">
                                <div>
                                    <h5>Openings</h5>
                                    <p>Stages for your candidates to follow</p>
                                </div>
                                <div>
                                    <Link to="/company/openings/create-job" className="btn btn-primary fs-7 flex-shrink-0">Create Job</Link>   
                                </div>
                            </div>

                            {data.map((item, ind) => {
                                return ( 
                                    <div key={item.id}>
                                        <div className="card card-border mb-3">
                                            <div className="card-header card-header-action">
                                                <h6>{ item.job_title}</h6>
                                                <div className="card-action-wrap">
                                                    <div className="dropdown">
                                                        <button aria-expanded="false" data-bs-toggle="dropdown" className="btn btn-success btn-sm dropdown-toggle d-flex align-items-center" type="button">Dropdown <span className="caret"></span></button>
                                                        <div role="menu" className="dropdown-menu">
                                                            <a className="dropdown-item" href="#.">Action</a>
                                                            <a className="dropdown-item" href="#.">Another action</a>
                                                            <a className="dropdown-item" href="#.">Something else here</a>
                                                            <div className="dropdown-divider"></div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="card-body">
                                                <div className="clearfix">
                                                    <div className="float-start">
                                                        <p>{item.positions} Positions - {item.job_type} - created on {item.created_at}</p>
                                                    </div>
                                                    <div className="float-end">
                                                        <p>{item.applicants.length} Candidates</p>
                                                    </div>
                                                </div>
                                                <ul class="list-group list-group-horizontal mt-3">
                                                    {item.pipline.map(i => <li class="list-group-item" key={i.id}>{i.name }</li>)}
                                                    
                                                </ul>
                                            </div>
                                            <div class="card-footer">
                                                <div className="clearfix">
                                                    <div className="float-start">
                                                        hello
                                                    </div>
                                                    <div className="float-end">
                                                        <p key={ind}>Hello <Link to={`/company/openings/dashboard/${item.id}`}>View</Link></p>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    )
                            })}
						</>
					}
                    </div>							
                </div>
            </div>
        </>
    )
}