export const toggleButton = () => {
    var dm = document.getElementById('main-1')
    
    if (dm.getAttribute('data-layout-style') === 'default') {
        dm.setAttribute('data-layout-style', 'collapsed')
        dm.setAttribute('data-hover', 'active')
    } else {
        dm.setAttribute('data-layout-style', 'default')
        dm.removeAttribute('data-hover')
    }
}

export const toggleBack = () => {
    var d2 = document.getElementById('hk_menu_backdrop')
     d2.addEventListener('click',()=>{
        
        var d = document.getElementById('main-1')
        d.setAttribute('data-layout-style', 'default')
         d.removeAttribute('data-hover')
     })
}

export const togglePhone = () => {
    var d = document.getElementById('main-1')
    d.setAttribute('data-layout-style', 'default')
    d.removeAttribute('data-hover')
}