import { BASE_MEDIA_URL } from "../../../authentication/api"


export const Recruiters = (props) => {
  
    return (
        <>
            <div className="mt-3 ms-5 me-5 mb-3">
                <table className="table">
                    <thead>
                        <tr>
                        <th scope="col"></th>
                        <th scope="col">Full Name</th>
                        <th scope="col">Phone</th>
                        <th scope="col">Email</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.data.recruiters.map((item, ind) => {
                            return (
                                <tr key={ind}>
                                    <th scope="row">
                                        <div className="avatar avatar-rounded position-relative avatar-sm">
                                            <img src={`${BASE_MEDIA_URL}${item.avatar}`} alt="user" className="avatar-img" />
                                        </div>               
                                    </th>
                                    <td>
                                        {item.first_name} {item.last_name}
                                    </td>
                                    <td>{item.phone_number}</td>
                                    <td>sample@gmail.com</td>
                                </tr>    
                            )
                        })}
                    </tbody>
                </table>
            </div>
        </>
    )
}