import { useFetchData } from "../../../../widget/useFetchData"
import { LoadingCircle, RetryAgain } from "../../../../widget/Loading"
import { SubmitButton } from "../../../../widget/spinner"
import { useState } from "react"
import { useForm } from "react-hook-form"
import instance from "../../../../authentication/axiosInstance"
import { Link } from "react-router-dom"


export const IndividualProfile = () => {
    
    const URL = `agent/profile/get-individual-profile/`
    const {error, isLoaded, data, fetchData} = useFetchData(URL)
	const [spin, setSpin] = useState(false);
	const { register, handleSubmit, formState: { errors } } = useForm();
	const onSubmit = data => {
		// console.log(data);
		setSpin(true);
		const url = `agent/profile/update-personal-details/`
		instance.post(url, data)
			.then((response) => {
                setSpin(false);
				
			}).catch((error) => {
				setSpin(false);
				// console.log(error)
			})
	};
	
    return (
        <>
            <div className="m-3">
                <div className="clearfix mb-3">
                    <div className="float-end mb-3">
                        <div className="">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb">
                                    <li className="breadcrumb-item"><Link>Settings</Link></li>
                                    <li className="breadcrumb-item"><Link>Profile</Link></li>
                                    <li className="breadcrumb-item active" aria-current="page">Personal Details</li>
                                </ol>
                            </nav>
                        </div>
                        
                    </div>
                    <div className="float-start">
                        <h5>Personal Details</h5>
                    </div>
                </div>
                {
                    error !== null ?
                        <RetryAgain func={fetchData} error={error} />
                        : !isLoaded ? <LoadingCircle /> :
                            <>
                            <div>
                                <div className="row">
                                    <div className="col-sm-10 position-relative mx-auto">
                                        <div>
                                            <form className="w-100" onSubmit={handleSubmit(onSubmit)} noValidate>
                                                <div className="row">
                                                    <div className="col-lg-10 col-md-10 col-sm-10 mx-auto">
                                                        
                                                        <div className="card card-lg card-border">
                                                            <div className="card-body">
                                                                <div className="row gx-3">

                                                                    <div className="form-group col-lg-12">
                                                                        <div className="form-label-group">
                                                                            <label>First Name</label>
                                                                        </div>
                                                                        <input className="form-control" placeholder="First Name" type="text" {...register("first_name", { required: true })} defaultValue={data.first_name} />
                                                                        {errors.first_name && <p style={{color:"red"}}>This field is required</p>}
                                                                    </div>
                                                                    <div className="form-group col-lg-12">
                                                                        <div className="form-label-group">
                                                                            <label>Last Name</label>
                                                                        </div>
                                                                        <input className="form-control" placeholder="Last Name" type="text" {...register("last_name", { required: true })} defaultValue={data.last_name} />
                                                                        {errors.last_name && <p style={{color:"red"}}>This field is required</p>}
                                                                    </div>
                                                                </div>
                                                                <div className="form-group col-lg-12">
                                                                    <div className="form-label-group">
                                                                        <label>Phone Number</label>
                                                                    </div>
                                                                    <input className="form-control" placeholder="Phone Number" type="tel" {...register("phone", { required: true })} defaultValue={data.phone_number} />
                                                                    {errors.phone && <p style={{color:"red"}}>This field is required</p>}
                                                                </div>
                                                                
                                                                {spin?<SubmitButton />: <button className="btn btn-primary btn-sm" type='submit'>Update</button>}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                    </>}
            </div>
        </>
    )
}