import logo from '../../assets/logo/icon.png';
import { Link } from 'react-router-dom';
import { useForm } from "react-hook-form";
import { useState } from 'react';
import { LoginSpinner } from '../widget/spinner';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { BASEURL } from '../authentication/api';
import { useAuthContext } from '../authentication/useAuthContext';
import { Navigate } from "react-router-dom";
import { HOMEPATH } from '../authentication/api';

export const Signup = () => {
	const navigate = useNavigate();
	const [duplicate, setDuplicate] = useState(false);
	const [spin, setSpin] = useState(false);
	const { register, handleSubmit, watch, formState: { errors } } = useForm();
	const onSubmit = data => {
		// console.log(data);
		setSpin(true);
		const url = `${BASEURL}agent/create-new-agent/`
		axios.post(url, data)
			.then((response) => {
				setSpin(false);
				  let token = response.data.access;
				// console.log(response.data)
				// console.log("token---------")
				// console.log(token)
				localStorage.setItem("token", token);
				navigate('/onboarding')
				
			}).catch((error) => {
				setSpin(false);
				setDuplicate(true);
				console.log(error)
			})
	
	};
	
	
    const { user } = useAuthContext();
    if (user) {
        return <Navigate replace to={HOMEPATH} />;
    }
	return (
		<>
			<div className="hk-wrapper hk-pg-auth" data-footer="simple">
				{/* <!-- Main Content --> */}
				<div className="hk-pg-wrapper pt-0 pb-xl-0 pb-5">
					<div className="hk-pg-body pt-0 pb-xl-0">
						{/* <!-- Container --> */}
						<div className="container-xxl">
							{/* <!-- Row --> */}
							<div className="row">
								<div className="col-sm-10 position-relative mx-auto">
									<div className="auth-content py-8">
										<form className="w-100" onSubmit={handleSubmit(onSubmit)} noValidate>
											<div className="row">
												<div className="col-lg-5 col-md-7 col-sm-10 mx-auto">
													<div className="text-center mb-7">
														<a className="navbar-brand me-0" href="index.html">
															<img className="brand-img d-inline-block" src={logo} alt="brand" height="40px" />
															{/* <img classNameName="brand-img img-fluid" src={logo} width="40px" height="40px" alt="brand" />
                                                    <span classNameName="brand-img img-fluid mt-1" style={{fontWeight:"bolder", fontSize:"30px"}}>Applicant</span> */}
														</a>
													</div>
													<div className="card card-lg card-border">
														<div className="card-body">
															<h4 className="mb-4 text-center">Sign up to create new account</h4>
															<div className="row gx-3">
																<div className="col-lg-12">
																	{duplicate ?
																	<div className="alert alert-danger" role="alert">
																		User with this email already exist.
																	</div>
																		: ""}
																	
																</div>
																<div className="form-group col-lg-12">
																	<div className="form-label-group">
																		<label>Email</label>
																	</div>
																	<input className="form-control" placeholder="Email" type="email" {...register("email", { required: true })} />
                                                                    {errors.email && <p style={{color:"red"}}>Email is required</p>}
                                                                </div>
																<div className="form-group col-lg-12">
                                                                    <div className="form-label-group">
																		<label>Password</label>
																	</div>
																	<div className="input-group password-check">
																		<span className="input-affix-wrapper">
																			<input className="form-control" placeholder="Password" type="password"  {...register("password", { required: true, minLength:8 })}/>
																			<Link className="input-suffix text-muted">
																				<span className="feather-icon"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-eye form-icon"><path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path><circle cx="12" cy="12" r="3"></circle></svg></span>
																				<span className="feather-icon d-none"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-eye-off form-icon"><path d="M17.94 17.94A10.07 10.07 0 0 1 12 20c-7 0-11-8-11-8a18.45 18.45 0 0 1 5.06-5.94M9.9 4.24A9.12 9.12 0 0 1 12 4c7 0 11 8 11 8a18.5 18.5 0 0 1-2.16 3.19m-6.72-1.07a3 3 0 1 1-4.24-4.24"></path><line x1="1" y1="1" x2="23" y2="23"></line></svg></span>
																			</Link>
																		</span>
                                                                    </div>
                                                                    {errors.password && <p style={{color:"red"}}>{watch("password")===""?"This field is required":"Password length should be at least 8 characters"} </p>}
																</div>
                                                                <div className="form-group col-lg-12">
                                                                    <div className="form-label-group">
																		<label>Re-type  Password</label>
																	</div>
																	<div className="input-group password-check">
																		<span className="input-affix-wrapper">
																			<input className="form-control" placeholder="Re-type  Password" type="password"  {...register("password2", { required: true , validate:()=>watch("password")===watch("password2")})}/>
																			<Link className="input-suffix text-muted">
																				<span className="feather-icon"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-eye form-icon"><path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path><circle cx="12" cy="12" r="3"></circle></svg></span>
																				<span className="feather-icon d-none"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-eye-off form-icon"><path d="M17.94 17.94A10.07 10.07 0 0 1 12 20c-7 0-11-8-11-8a18.45 18.45 0 0 1 5.06-5.94M9.9 4.24A9.12 9.12 0 0 1 12 4c7 0 11 8 11 8a18.5 18.5 0 0 1-2.16 3.19m-6.72-1.07a3 3 0 1 1-4.24-4.24"></path><line x1="1" y1="1" x2="23" y2="23"></line></svg></span>
																			</Link>
																		</span>
                                                                    </div>
                                                                    {errors.password2 && <p style={{ color: "red" }}>{ watch("password")!==watch("password2")?"Passwords do not match":"This field is required"}</p>}
																</div>
															</div>
															
                                                            
                                                            {spin?<LoginSpinner />: <button className="btn btn-primary btn-uppercase btn-block" type='submit'>Signup</button>}
															{/* <p className="p-xs mt-2 text-center">or</p>
															<a className="btn btn-block text-uppercase btn-outline-light" href="#."><img src="https://img.icons8.com/color/16/000000/google-logo.png" alt="google" /> Signup Using Google</a> */}
															<p className="p-xs mt-2 text-center">Already have account? <Link to="/"><u>Login</u></Link></p>
														</div>
													</div>
												</div>
											</div>
										</form>
									</div>
								</div>
							</div>
							{/* <!-- /Row --> */}
						</div>
						{/* <!-- /Container --> */}
					</div>
					{/* <!-- /Page Body --> */}

					{/* <!-- Page Footer --> */}
					<div className="hk-footer border-0">
						<footer className="container-xxl footer">
							<div className="row">
								<div className="col-xl-8 text-center">
									<p className="footer-text pb-0"><span className="copy-text">Zestol © 2022 All rights reserved.</span> <a href="#.." className="" target="_blank">Privacy Policy</a><span className="footer-link-sep">|</span><a href="#.." className="" target="_blank">T&amp;C</a><span className="footer-link-sep">|</span><a href="#.." className="" target="_blank">System Status</a></p>
								</div>
							</div>
						</footer>
					</div>
					{/* <!-- / Page Footer --> */}

				</div>
				{/* <!-- /Main Content --> */}
			</div>
		</>
	)
}