import { Link } from "react-router-dom"
import instance from "../../authentication/axiosInstance"

export const InviteCandidate = () => {
    function postData() {
        instance.post("agent/candidate/invite-new-candidate/",{
            firstName: 'Fred',
            lastName: 'Flintstone'
        }).then(function (response) {
            console.log(response);
        })
        .catch(function (error) {
            console.log(error);
        });        
    }

    return (
        <>
														
            <button type="button" className="btn btn-primary" data-bs-toggle="modal" data-bs-target="#exampleModal">
                Invite Candidate
            </button>

            <div className="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="exampleModalLabel">Invite Candidate</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <h6>Email</h6>
                            <input type="email" name="email" id="email" placeholder="Email" className="form-control" />
                            <div className="row mt-3">
                                <div className="col"></div>
                                <div className="col"><Link className="btn btn-link">Copy invitation link</Link></div>
                            </div>
                            <div className="row mt-3 mb-3">
                                <div className="col"><button className="btn btn-soft-primary btn-sm btn-block" data-bs-dismiss="modal">Cancel</button></div>
                                <div className="col"><button className="btn btn-primary btn-sm btn-block" onClick={postData}>Invite</button></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}