import React from 'react';
import candidates from "../../assets/images/candidates.png";
import performance from "../../assets/images/performance.png";
import { useAuthContext } from '../authentication/useAuthContext';
import { LOGINPATH } from '../authentication/api';
import { Navigate } from 'react-router-dom';
import { Link } from 'react-router-dom';


export const Report = () => {
    const { user } = useAuthContext();
    
    if (!user) {
        return <Navigate replace to={LOGINPATH} />;
    }
    return (
        <div className="m-3">
            <div className="row">
              <div className="col-6">
                <Link style={{color: "grey"}} to="/company/reports/website" className="card card-border card-int">
                  <div className="card-body">
                    <div className="media align-items-center">
                      <div className="media-head me-3">
                        <div className="avatar avatar-xl avatar-icon avatar-soft-success avatar-rounded">
                          <img alt="candidates" src={candidates}  className="avatar-img" />
                        </div>
                      </div>
                      <div className="media-body">
                        <h6 className="mb-0">Website</h6>
                        <p>Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae</p>
                      </div>
                    </div>
                  </div>
                </Link>
              </div>
              <div className="col-6">
                <Link style={{color: "grey"}} to="/company/reports/performance" className="card card-border card-int">
                  <div className="card-body">
                    <div className="media align-items-center">
                      <div className="media-head me-3">
                        <div className="avatar avatar-xl avatar-icon avatar-soft-success avatar-rounded">
                          <img alt="performance" src={performance}  className="avatar-img" />
                        </div>
                      </div>
                      <div className="media-body">
                        <h6 className="mb-0">Performance</h6>
                        <p>Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae</p>
                      </div>
                    </div>
                  </div>
                </Link>
              </div>
            </div>
        </div>
    )
}
