import { Link } from "react-router-dom"
import { useState } from "react"
import { useFetchData } from "../../widget/useFetchData"
import { useForm } from "react-hook-form"
import instance from "../../authentication/axiosInstance"
import { LoadingCircle, RetryAgain } from "../../widget/Loading"
import { SubmitButton } from "../../widget/spinner"


export const OrganizationalSettings = () => {
    
    const URL = `agent/profile/get-business-profile/`
    const {error, isLoaded, data, fetchData} = useFetchData(URL)
	const [spin, setSpin] = useState(false);
	const { register, handleSubmit, formState: { errors } } = useForm();
	const onSubmit = data => {
		// console.log(data);
		setSpin(true);
		const url = `agent/profile/update-business-details/`
		instance.post(url, data)
			.then((response) => {
                setSpin(false);
				
			}).catch((error) => {
				setSpin(false);
				// console.log(error)
			})
	};
    return (
        <>
            <div className="m-3">
                <div className="clearfix mb-3">
                    <div className="float-end mb-3">
                        <div className="">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb">
                                    <li className="breadcrumb-item"><Link to="/company/settings">Settings</Link></li>
                                    <li className="breadcrumb-item active" aria-current="page">Organization</li>
                                </ol>
                            </nav>
                        </div>
                        
                    </div>
                    <div className="float-start">
                        <h5>Organization</h5>
                    </div>
                </div>
                {
                    error !== null ?
                        <RetryAgain func={fetchData} error={error} />
                        : !isLoaded ? <LoadingCircle /> :
                            <>
                            <div>
                                <div className="row">
                                    <div className="col-sm-10 position-relative mx-auto">
                                        <div>
                                            <form className="w-100" onSubmit={handleSubmit(onSubmit)} noValidate>
                                                <div className="row">
                                                    <div className="col-lg-10 col-md-10 col-sm-10 mx-auto">
                                                        
                                                        <div className="card card-lg card-border">
                                                            <div className="card-body">
                                                                <div className="row gx-3">

                                                                    <div className="form-group col-lg-12">
                                                                        <div className="form-label-group">
                                                                            <label>Business Name</label>
                                                                        </div>
                                                                        <input className="form-control" placeholder="Business Name" type="text" {...register("name", { required: true })} defaultValue={data.name} />
                                                                        {errors.name && <p style={{color:"red"}}>This field is required</p>}
                                                                    </div>
                                                                    <div className="form-group col-lg-12">
                                                                        <div className="form-label-group">
                                                                            <label>Business Email</label>
                                                                        </div>
                                                                        <input className="form-control" placeholder="Business Email" type="text" {...register("email", { required: true })} defaultValue={data.email} />
                                                                        {errors.email && <p style={{color:"red"}}>This field is required</p>}
                                                                    </div>
                                                                </div>
                                                                <div className="form-group col-lg-12">
                                                                    <div className="form-label-group">
                                                                        <label>Phone Number</label>
                                                                    </div>
                                                                    <input className="form-control" placeholder="Phone Number" type="tel" {...register("phone", { required: true })} defaultValue={data.phone_number} />
                                                                    {errors.phone && <p style={{color:"red"}}>This field is required</p>}
                                                                </div>
                                                                <div className="form-group col-lg-12">
                                                                    <div className="form-label-group">
                                                                        <label>Account Type</label>
                                                                    </div>
                                                                    <input className="form-control" placeholder="Account Type" {...register("account_type", { required: true })} defaultValue={data.account_type} />
                                                                    {/* {errors.phone && <p style={{color:"red"}}>This field is required</p>} */}
                                                                </div>  
                                                                <div className="form-group col-lg-12">
                                                                    <div className="form-label-group">
                                                                        <label>Career page url</label>
                                                                    </div>
                                                                    <input className="form-control" placeholder="Phone Number" type="tel" {...register("career_page", { required: true })} defaultValue={data.career_page} />
                                                                    {/* {errors.phone && <p style={{color:"red"}}>This field is required</p>} */}
                                                                </div>
                                                                <div className="form-group col-lg-12">
                                                                    <div className="form-label-group">
                                                                        <label>Description</label>
                                                                        </div>
                                                                        <textarea className="form-control" placeholder="Description" {...register("description", { required: true })} defaultValue={data.description} cols="30" rows="10"></textarea>

                                                                    </div>   
                                                                
                                                                {spin?<SubmitButton />: <button className="btn btn-primary btn-sm" type='submit'>Update</button>}
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                    </>}
            </div>
        </>
    )
}
