import { BASE_MEDIA_URL } from "../../../authentication/api"


export const Comments = (props) => {
    return (
        <>
            <div className="m-3">
                <div>
                    {props.data.comments.map((item, ind)=>{
                        return (
                        <div className="card-border card mb-3">
                            <div className="card-body">
                                <div className="mb-2 clearfix">
                                    <div className="float-start">
                                        <div className="media align-items-center">
                                            <div className="media-head me-2">
                                                <div className="avatar avatar-xs avatar-rounded">
                                                    <img src={`${BASE_MEDIA_URL}${item.sender.avatar}`} alt={item.sender.first_name} className="avatar-img" />
                                                </div>
                                            </div>
                                            <div className="media-body">
                                                <div className="text-high-em">{item.sender.first_name} {item.sender.last_name} </div> 
                                                <p className="fs-7">{item.sender.email} </p> 
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <p>{item.comment}</p>
                            </div>
                        </div>
                            
                        )
                    })}
                </div>
            </div>
        </>
    )
}