import DataTableBase from '../../widget/dataTable';
import { Link } from "react-router-dom";
import { LoadingCircle, RetryAgain } from "../../widget/Loading";
import { useFetchData } from "../../widget/useFetchData";

const columns = [
  {
    name: "Job Opening",
    selector: (row) => row.job_title,
        sortable: true,
        cell: (row) => (
            <>
                <div className="media align-items-center">
                    <div className="media-body">
                        <div className="text-high-em"><Link to={`/company/candidates/application/${row.id}`}>{row.job_title}</Link></div> 
                        <p className="fs-7">{row.job_type}</p> 
                    </div>
                </div>	 
            </>
        )
    },
    {
        name: "Level",
        selector: (row) => 'row.level',
        sortable: true,
    },
    {
        name: "Positions",
        selector: (row) => row.positions.length,
        sortable: true,
        center: true,
    },
    {
        name: "Candidates",
        selector: (row) => row.applicants.length,
        sortable: true,
        center: true,
    },
  {
    name: "Status",
    selector: (row) => 'row.status',
    sortable: true,
    },


];


export const JobSubmission = () => {
    const URL = `agent/openings/get-job-opening-list/`
    const { error, isLoaded, data, fetchData} = useFetchData(URL)

    return (
        <>
            <div>
                <div className="mt-3 ms-5 me-5">			
                    <div>
					{
						error !== null ?
							<RetryAgain func={fetchData} error={error} />
							: !isLoaded?<LoadingCircle />:
						<>
							<div className="d-flex flex-md-nowrap flex-wrap align-items-center justify-content-between mb-2">
								<div>
									<h5>Applications</h5>
									<p>Manage all applications received for specific role</p>
								</div>
							</div>
							<DataTableBase
								columns={columns}
								data={data}
							/>
						</>
					}
                    </div>
													
                </div>
            </div>
        </>
    )
}