import logo from '../../assets/logo/icon.png'; 
import { Link } from 'react-router-dom';
import { useAuthContext } from '../authentication/useAuthContext';
import { Navigate } from "react-router-dom";
import { HOMEPATH } from '../authentication/api';
import axios from 'axios';
import { GetBasePath } from '../authentication/api';
import { useState } from 'react'; 
import { useNavigate } from 'react-router-dom';
import { useForm } from "react-hook-form";
import { LoginSpinner } from '../widget/spinner';


export const Login = () => {
	const { user, setUser } = useAuthContext();
	const navigate = useNavigate();
	const [duplicate, setDuplicate] = useState(false);
	const [spin, setSpin] = useState(false);
	const { register, handleSubmit, formState: { errors } } = useForm();
	const onSubmit = data => {
		// console.log(data);
		setSpin(true);
		axios.post(GetBasePath("accounts/token/"), data)
			.then((response) => {
				setSpin(false);
				  let token = response.data.access;
				// console.log(response.data)
				
				localStorage.setItem("token", token);
				setUser(token)
				navigate(HOMEPATH)
				
			}).catch((error) => {
				setSpin(false);
				setDuplicate(true);
				// console.log(error.response.status)
			})
	
	};

    // const { user, setUser } = useAuthContext();
    if (user) {
        return <Navigate replace to={HOMEPATH} />;
	}
	
	return (
		<>
			<div className="hk-wrapper hk-pg-auth" data-footer="simple">
				{/* <!-- Main Content --> */}
				<div className="hk-pg-wrapper pt-0 pb-xl-0 pb-5">
					<div className="hk-pg-body pt-0 pb-xl-0">
						{/* <!-- Container --> */}
						<div className="container-xxl">
							{/* <!-- Row --> */}
							<div className="row">
								<div className="col-sm-10 position-relative mx-auto">
									<div className="auth-content py-8">
										<form className="w-100" onSubmit={handleSubmit(onSubmit)} noValidate>
											<div className="row">
												<div className="col-lg-5 col-md-7 col-sm-10 mx-auto">
													<div className="text-center mb-7">
														<a className="navbar-brand me-0" href="index.html">
															<img className="brand-img d-inline-block" src={logo} alt="brand" height="40px" />
															{/* <img className="brand-img img-fluid" src={logo} width="40px" height="40px" alt="brand" />
                                                    <span className="brand-img img-fluid mt-1" style={{fontWeight:"bolder", fontSize:"30px"}}>Applicant</span> */}
														</a>
													</div>
													<div className="card card-lg card-border">
														<div className="card-body">
															<h4 className="mb-4 text-center">Sign in to your account-</h4>
															<div className="col-lg-12">
																	{duplicate ?
																	<div className="alert alert-danger" role="alert">
																		Email or password is incorrect.
																	</div>
																		: ""}
																	
																</div>
															<div className="row gx-3">
																<div className="form-group col-lg-12">
																	<div className="form-label-group">
																		<label>Email</label>
																	</div>
																	<input className="form-control" placeholder="Enter email" type="text"  {...register("username", { required: true })} />
																	{errors.username && <p style={{color:"red"}}>Email is required</p>}
																</div>
																<div className="form-group col-lg-12">
																	<div className="form-label-group">
																		<label>Password</label>
																		<Link to="/forgot-password" className="fs-7 fw-medium">Forgot Password ?</Link>
																	</div>
																	<div className="input-group password-check">
																		<span className="input-affix-wrapper">
																			<input className="form-control" placeholder="Enter your password" type="password"  {...register("password", { required: true })} />
																			
																		</span>
																		{errors.password && <p style={{color:"red"}}>Password is required</p>}
																	</div>
																</div>
															</div>
															
															{spin?<LoginSpinner />: <button className="btn btn-primary btn-uppercase btn-block" type='submit'>Login</button>}
															<p className="p-xs mt-2 text-center">or</p>
															<a className="btn btn-block text-uppercase btn-outline-light" href="#."><img src="https://img.icons8.com/color/16/000000/google-logo.png" alt="google" /> Signup Using Google</a>
															<p className="p-xs mt-2 text-center">Don't have account? <Link to="/signup"><u>Create new account</u></Link></p>
														</div>
													</div>
												</div>
											</div>
										</form>
									</div>
								</div>
							</div>
							{/* <!-- /Row --> */}
						</div>
						{/* <!-- /Container --> */}
					</div>
					{/* <!-- /Page Body --> */}

					{/* <!-- Page Footer --> */}
					<div className="hk-footer border-0">
						<footer className="container-xxl footer">
							<div className="row">
								<div className="col-xl-8 text-center">
									<p className="footer-text pb-0"><span className="copy-text">Zestol © 2022 All rights reserved.</span> <a href="#.." className="" target="_blank">Privacy Policy</a><span className="footer-link-sep">|</span><a href="#.." className="" target="_blank">T&amp;C</a><span className="footer-link-sep">|</span><a href="#.." className="" target="_blank">System Status</a></p>
								</div>
							</div>
						</footer>
					</div>
					{/* <!-- / Page Footer --> */}

				</div>
				{/* <!-- /Main Content --> */}
			</div>
		</>
	)
}