import React from 'react';

// Blatant "inspiration" from https://codepen.io/Jacqueline34/pen/pyVoWr
function convertArrayOfObjectsToCSV(array, column) {
	let result;

	const columnDelimiter = ',';
	const lineDelimiter = '\n';
	const keys = column;

	result = '';
	result += keys.join(columnDelimiter);
    result += lineDelimiter;
    
    // console.log(array)
    // console.log(keys)

	array.forEach(item => {
		let ctr = 0;
		keys.forEach(key => {
			if (ctr > 0) result += columnDelimiter;

			result += item[key];
			// eslint-disable-next-line no-plusplus
			ctr++;
		});
		result += lineDelimiter;
	});

	return result;
}

// Blatant "inspiration" from https://codepen.io/Jacqueline34/pen/pyVoWr
export function downloadCSV(array, column, file) {
	const link = document.createElement('a');
	let csv = convertArrayOfObjectsToCSV(array, column);
	if (csv == null) return;

	const filename = `${file}.csv`;

	if (!csv.match(/^data:text\/csv/i)) {
		csv = `data:text/csv;charset=utf-8,${csv}`;
	}

	link.setAttribute('href', encodeURI(csv));
	link.setAttribute('download', filename);
	link.click();
}

// eslint-disable-next-line react/prop-types
export const Export = ({ onExport }) => <button className="btn btn-primary" onClick={e => onExport(e.target.value)}>Export</button>;

