import logo from '../../assets/logo/icon.png';
import { useForm } from "react-hook-form";
import { useState } from 'react';
import { LoginSpinner } from '../widget/spinner';
import { useNavigate } from 'react-router-dom';
import { HOMEPATH } from '../authentication/api';
import instance from '../authentication/axiosInstance';


export const Onboarding = (props) => {
    const [business, setBusiness] = useState(false);
    return (
        <>
            {business===true?<BusinessDetails />:<PersonalDetails setBusiness={setBusiness} />} 
        </>
    )
}

export const BusinessDetails = (props) => {
	const navigate = useNavigate();
	const [spin, setSpin] = useState(false);
	const { register, handleSubmit, formState: { errors } } = useForm();
	const onSubmit = data => {
		// console.log(data);
		setSpin(true);
		const url = `agent/profile/update-business-details/`
		instance.post(url, data)
			.then((response) => {
                setSpin(false);

				navigate(HOMEPATH)
				
			}).catch((error) => {
				setSpin(false);
				// console.log(error)
			})
	
	};
    return (
		<>
			<div className="hk-wrapper hk-pg-auth" data-footer="simple">
				{/* <!-- Main Content --> */}
				<div className="hk-pg-wrapper pt-0 pb-xl-0 pb-5">
					<div className="hk-pg-body pt-0 pb-xl-0">
						{/* <!-- Container --> */}
						<div className="container-xxl">
							{/* <!-- Row --> */}
							<div className="row">
								<div className="col-sm-10 position-relative mx-auto">
									<div className="auth-content py-8">
										<form className="w-100" onSubmit={handleSubmit(onSubmit)} noValidate>
											<div className="row">
												<div className="col-lg-5 col-md-7 col-sm-10 mx-auto">
													<div className="text-center mb-7">
														<a className="navbar-brand me-0" href="index.html">
															<img className="brand-img d-inline-block" src={logo} alt="brand" height="40px" />
														</a>
													</div>
													<div className="card card-lg card-border">
														<div className="card-body">
															<h4 className="mb-4 text-center">Business Details</h4>
															<div className="row gx-3">

																<div className="form-group col-lg-12">
																	<div className="form-label-group">
																		<label>Business Name</label>
																	</div>
																	<input className="form-control" placeholder="Business Name" type="text" {...register("name", { required: true })} />
                                                                    {errors.name && <p style={{color:"red"}}>This field is required</p>}
                                                                </div>
																<div className="form-group col-lg-12">
																	<div className="form-label-group">
																		<label>Location</label>
																	</div>
																	<input className="form-control" placeholder="Location" type="text" {...register("location", { required: true })} />
                                                                    {errors.location && <p style={{color:"red"}}>This field is required</p>}
                                                                </div>
															</div>
                                                            <div className="form-group col-lg-12">
                                                                <div className="form-label-group">
                                                                    <label>Phone Number</label>
                                                                </div>
                                                                <input className="form-control" placeholder="Phone Number" type="tel" {...register("phone", { required: true })} />
                                                                {errors.phone && <p style={{color:"red"}}>This field is required</p>}
                                                            </div>
                                                            
                                                            {spin?<LoginSpinner />: <button className="btn btn-primary btn-uppercase btn-block" type='submit'>Complete</button>}
														</div>
													</div>
												</div>
											</div>
										</form>
									</div>
								</div>
							</div>
							{/* <!-- /Row --> */}
						</div>
						{/* <!-- /Container --> */}
					</div>

				</div>
				{/* <!-- /Main Content --> */}
			</div>
		</>
    )
}

export const PersonalDetails = (props) => {
	const [spin, setSpin] = useState(false);
	const { register, handleSubmit, formState: { errors } } = useForm();
	const onSubmit = data => {
		// console.log(data);
		setSpin(true);
		const url = `agent/profile/update-personal-details/`
		instance.post(url, data)
			.then((response) => {
                setSpin(false);
                props.setBusiness(true)
				
			}).catch((error) => {
				setSpin(false);
				// console.log(error)
			})
	
	};
	
	
    return (
		<>
			<div className="hk-wrapper hk-pg-auth" data-footer="simple">
				{/* <!-- Main Content --> */}
				<div className="hk-pg-wrapper pt-0 pb-xl-0 pb-5">
					<div className="hk-pg-body pt-0 pb-xl-0">
						{/* <!-- Container --> */}
						<div className="container-xxl">
							{/* <!-- Row --> */}
							<div className="row">
								<div className="col-sm-10 position-relative mx-auto">
									<div className="auth-content py-8">
										<form className="w-100" onSubmit={handleSubmit(onSubmit)} noValidate>
											<div className="row">
												<div className="col-lg-5 col-md-7 col-sm-10 mx-auto">
													<div className="text-center mb-7">
														<a className="navbar-brand me-0" href="index.html">
															<img className="brand-img d-inline-block" src={logo} alt="brand" height="40px" />
														</a>
													</div>
													<div className="card card-lg card-border">
														<div className="card-body">
															<h4 className="mb-4 text-center">Personal Details</h4>
															<div className="row gx-3">

																<div className="form-group col-lg-12">
																	<div className="form-label-group">
																		<label>First Name</label>
																	</div>
																	<input className="form-control" placeholder="First Name" type="text" {...register("first_name", { required: true })} />
                                                                    {errors.first_name && <p style={{color:"red"}}>This field is required</p>}
                                                                </div>
																<div className="form-group col-lg-12">
																	<div className="form-label-group">
																		<label>Last Name</label>
																	</div>
																	<input className="form-control" placeholder="Last Name" type="text" {...register("last_name", { required: true })} />
                                                                    {errors.last_name && <p style={{color:"red"}}>This field is required</p>}
                                                                </div>
															</div>
                                                            <div className="form-group col-lg-12">
                                                                <div className="form-label-group">
                                                                    <label>Phone Number</label>
                                                                </div>
                                                                <input className="form-control" placeholder="Phone Number" type="tel" {...register("phone", { required: true })} />
                                                                {errors.phone && <p style={{color:"red"}}>This field is required</p>}
                                                            </div>
                                                            
                                                            {spin?<LoginSpinner />: <button className="btn btn-primary btn-uppercase btn-block" type='submit'>Next</button>}
														</div>
													</div>
												</div>
											</div>
										</form>
									</div>
								</div>
							</div>
							{/* <!-- /Row --> */}
						</div>
						{/* <!-- /Container --> */}
					</div>
					
				</div>
				{/* <!-- /Main Content --> */}
			</div>
		</>
    )
}