import { toggleButton, toggleBack } from "../funcs/toggle"
import { mainRoutes } from "../routes"
import { Link, useLocation } from "react-router-dom";
import { togglePhone } from "../funcs/toggle";
import logo from '../../assets/logo/icon.png';

export const SideBar = () => {
    const location = useLocation();
    return (
        <>
        <div className="hk-menu">
			{/* <!-- Brand --> */}
			<div className="menu-header">
				<span>
					<Link className="navbar-brand" to="/home">
						<img className="brand-img img-fluid" src={logo} width="32px" height="32px" alt="brand" />
						<span className="brand-img img-fluid">Sample</span>
					</Link>
					<button className="btn btn-icon btn-rounded btn-flush-dark flush-soft-hover navbar-toggle" onClick={toggleButton}>
						<span className="icon">
							<span className="svg-icon fs-5">
								<svg xmlns="http://www.w3.org/2000/svg" className="icon icon-tabler icon-tabler-arrow-bar-to-left" width="24" height="24" viewBox="0 0 24 24" strokeWidth="2" stroke="currentColor" fill="none" strokeLinecap="round" strokeLinejoin="round">
									<path stroke="none" d="M0 0h24v24H0z" fill="none"></path>
									<line x1="10" y1="12" x2="20" y2="12"></line>
									<line x1="10" y1="12" x2="14" y2="16"></line>
									<line x1="10" y1="12" x2="14" y2="8"></line>
									<line x1="4" y1="4" x2="4" y2="20"></line>
								</svg>
							</span>
						</span>
					</button>
				</span>
			</div>
			{/* <!-- /Brand --> */}

			{/* <!-- Main Menu --> */}
                <div data-simplebar="init" className="nicescroll-bar">
                    <div className="simplebar-wrapper" style={{ margin: "0px" }}>
                        <div className="simplebar-height-auto-observer-wrapper">
                            <div className="simplebar-height-auto-observer">
                            </div>
                        </div>
                        <div className="simplebar-mask">
                            <div className="simplebar-offset" style={{ right: "0px", bottom: "0px" }}>
                                <div className="simplebar-content-wrapper" style={{ height: "100%", overflow: "hidden scroll" }}>
                                    <div className="simplebar-content" style={{ padding: "0px" }}>
                                        <div className="menu-content-wrap">
                                            <div className="menu-group">
                                                
                                                <ul className="navbar-nav flex-column">
                                                    {mainRoutes.map((route, ind) => {
                                                        var condition = location.pathname.startsWith(route.path);
                                                    
                                                    return (
                                                    <li key={ind.toString()} className={condition?"nav-item active p-1":"nav-item p-1"}>
                                                        <Link className="nav-link" to={route.path} onClick={togglePhone}>
                                                            <span className="nav-icon-wrap">
                                                                <span className="svg-icon">
                                                                    <i className={route.icon} style={{fontSize:"20px"}}></i>
                                                                </span>
                                                            </span>
                                                            <span className="nav-link-text">{route.name}</span>
                                                        </Link>
                                                    </li>
                                                        )
                                                    })}	
                                                    
                                                </ul>
                                            </div>
                                            <div className="callout card card-flush bg-red-light-5 text-center mt-5 mx-auto">
                                                <div className="card-body p-2">
                                                    <p >14 days remain</p>
                                                    <Link className="btn btn-primary btn-block btn-sm" to="/company/settings/subscriptions">Click to subscribe</Link>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="simplebar-placeholder" style={{ width: "219px", height: "1117px" }}>
                        </div>
                    </div>
                    <div className="simplebar-track simplebar-horizontal" style={{ visibility: "hidden" }}>
                        <div className="simplebar-scrollbar" style={{ width: "0px", display: "none" }}>
                        </div>
                    </div>
                    <div className="simplebar-track simplebar-vertical" style={{ visibility: "visible" }}>
                        <div className="simplebar-scrollbar" style={{ height: "324px", transform: "translate3d(0px, 0px, 0px)", display: "block" }}>
                        </div>
                    </div>
                </div>
			{/* <!-- /Main Menu --> */}
		</div>
        <div id="hk_menu_backdrop" className="hk-menu-backdrop" onClick={toggleBack}></div>
        </>
    )
}