import React from 'react';
import themes from "../../assets/images/themes.png";
import emails from "../../assets/images/emails.png";
import details from "../../assets/images/details.png";
import { useAuthContext } from '../authentication/useAuthContext';
import { LOGINPATH } from '../authentication/api';
import { Navigate } from 'react-router-dom';
import { Link } from 'react-router-dom';


export const Career = () => {
    const { user } = useAuthContext();
    
    if (!user) {
        return <Navigate replace to={LOGINPATH} />;
    }
    return (
        <div className="m-3">
            <div className="row">
              <div className="col-6">
                <Link style={{color: "grey"}} to="/company/career-site/theme" className="card card-border card-int">
                  <div className="card-body">
                    <div className="media align-items-center">
                      <div className="media-head me-3">
                        <div className="avatar avatar-xl avatar-icon avatar-soft-success avatar-rounded">
                          <img alt="themes" src={themes}  className="avatar-img" />
                        </div>
                      </div>
                      <div className="media-body">
                        <h6 className="mb-0">Themes</h6>
                        <p>Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae</p>
                      </div>
                    </div>
                  </div>
                </Link>
              </div>
              <div className="col-6">
                <Link style={{color: "grey"}} to="/company/career-site/details" className="card card-border card-int">
                  <div className="card-body">
                    <div className="media align-items-center">
                      <div className="media-head me-3">
                        <div className="avatar avatar-xl avatar-icon avatar-soft-success avatar-rounded">
                          <img alt="details" src={details}  className="avatar-img" />
                        </div>
                      </div>
                      <div className="media-body">
                        <h6 className="mb-0">Details</h6>
                        <p>Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae</p>
                      </div>
                    </div>
                  </div>
                </Link>
              </div>
            </div>
            <div className="row">
              <div className="col-6">
                <Link style={{color: "grey"}} to="/company/career-site/emails" className="card card-border card-int">
                  <div className="card-body">
                    <div className="media align-items-center">
                      <div className="media-head me-3">
                        <div className="avatar avatar-xl avatar-icon avatar-soft-success avatar-rounded">
                          <img alt="emails" src={emails}  className="avatar-img" />
                        </div>
                      </div>
                      <div className="media-body">
                        <h6 className="mb-0">Emails</h6>
                        <p>Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae Venenatis pretium viverra suspendisse potenti nullam ac tortor vitae</p>
                      </div>
                    </div>
                  </div>
                </Link>
              </div>
            </div>
        </div>
    )
}