import { Link } from "react-router-dom"
import { useState } from "react";
import { useForm } from "react-hook-form";
import instance from "../../authentication/axiosInstance";
import { SubmitButton } from "../../widget/spinner";
import { Navigate } from "react-router-dom";


export const AddNewClient = () => {
	const [spin, setSpin] = useState(false);
	const { register, handleSubmit, formState: { errors } } = useForm();
	const onSubmit = data => {
		// console.log(data);
		setSpin(true);
		const url = `agent/clients/create-new-client/`
		instance.post(url, data)
			.then((response) => {
                setSpin(false);
                <Navigate replace to="/company/clients" />;
				
			}).catch((error) => {
				setSpin(false);
				// console.log(error)
			})
	};
    return (
        <>
            <div className="m-3">
                <div className="clearfix mb-3">
                    <div className="float-end mb-3">
                        <div className="">
                            <nav aria-label="breadcrumb">
                                <ol className="breadcrumb">
                                    <li className="breadcrumb-item"><Link to="/company/clients">Clients</Link></li>
                                    <li className="breadcrumb-item active" aria-current="page">Add Client</li>
                                </ol>
                            </nav>
                        </div>
                        
                    </div>
                    <div className="float-start">
                        <h5>Add Client</h5>
                    </div>
                </div>
                <div>
                    <div className="row">
                        <div className="col-sm-10 position-relative mx-auto">
                            <div>
                                <form className="w-100" onSubmit={handleSubmit(onSubmit)} noValidate>
                                    <div className="row">
                                        <div className="col-lg-10 col-md-10 col-sm-10 mx-auto">
                                            
                                            <div className="card card-lg card-border">
                                                <div className="card-body">
                                                    <div className="row gx-3">

                                                        <div className="form-group col-lg-12">
                                                            <div className="form-label-group">
                                                                <label>Client Name</label>
                                                            </div>
                                                            <input className="form-control" placeholder="Client Name" type="text" {...register("client", { required: true })} />
                                                            {errors.client && <p style={{color:"red"}}>This field is required</p>}
                                                        </div>
                                                        <div className="form-group col-lg-12">
                                                            <div className="form-label-group">
                                                                <label>Company</label>
                                                            </div>
                                                            <input className="form-control" placeholder="Company" type="text" {...register("company", { required: true })} />
                                                            {errors.company && <p style={{color:"red"}}>This field is required</p>}
                                                        </div>
                                                        <div className="form-group col-lg-12">
                                                            <div className="form-label-group">
                                                                <label>Industry</label>
                                                            </div>
                                                            <input className="form-control" placeholder="Industry" type="text" {...register("industry", { required: true })} />
                                                            {errors.industry && <p style={{color:"red"}}>This field is required</p>}
                                                        </div>
                                                        <div className="form-group col-lg-12">
                                                            <div className="form-label-group">
                                                                <label>Country</label>
                                                            </div>
                                                            <input className="form-control" placeholder="Country" type="text" {...register("country", { required: true })} />
                                                            {errors.country && <p style={{color:"red"}}>This field is required</p>}
                                                        </div>
                                                        <div className="form-group col-lg-12">
                                                            <div className="form-label-group">
                                                                <label>City</label>
                                                            </div>
                                                            <input className="form-control" placeholder="City" type="text" {...register("city", { required: true })} />
                                                            {errors.city && <p style={{color:"red"}}>This field is required</p>}
                                                        </div>
                                                        <div className="form-group col-lg-12">
                                                            <div className="form-label-group">
                                                                <label>Client Email</label>
                                                            </div>
                                                            <input className="form-control" placeholder="Client Email" type="text" {...register("email", { required: true })} />
                                                            {errors.email && <p style={{color:"red"}}>This field is required</p>}
                                                        </div>
                                                    </div>
                                                    <div className="form-group col-lg-12">
                                                        <div className="form-label-group">
                                                            <label>Client Number</label>
                                                        </div>
                                                        <input className="form-control" placeholder="Client Number" type="tel" {...register("phone", { required: true })}/>
                                                        {errors.phone && <p style={{color:"red"}}>This field is required</p>}
                                                    </div>
                                                    {spin?<SubmitButton />: <button className="btn btn-primary btn-sm" type='submit'>Add Client</button>}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}