import { Hiring } from "./openings/hiring/hiringDashboard";
import { Jobs } from "./openings/vaccancy/vaccancyDashboard";
import { useAuthContext } from '../authentication/useAuthContext';
import { LOGINPATH } from '../authentication/api';
import { Navigate } from 'react-router-dom';



export const Openings = () => {
    const { user } = useAuthContext();
    
    if (!user) {
        return <Navigate replace to={LOGINPATH} />;
    }
    return (
        <>
            <div style={{borderBottom: "1px solid #eaeaea"}}>
                <ul className="nav nav-line nav-light nav-tabs">
                    <li className="nav-item">
                        <a className="nav-link active" data-bs-toggle="tab" href="#jobs">
                            <span className="nav-link-text">Jobs</span>
                        </a>
                    </li>
                    <li className="nav-item">
                        <a className="nav-link" data-bs-toggle="tab" href="#hiring">
                            <span className="nav-link-text">Hiring Plan</span>
                        </a>
                    </li>
                </ul>
            
            </div>													
            <div className="tab-content">
                <div className="tab-pane fade show active" id="jobs">
                    <Jobs />
                </div>
                <div className="tab-pane fade" id="hiring">
                    <Hiring />
                </div>
            </div> 
        </>
    )
}