import { useCallback, useMemo } from "react";
import DataTableBase from '../widget/dataTable';
import { downloadCSV, Export } from '../widget/exports';
import { LoadingCircle } from "../widget/Loading";
import { RetryAgain } from "../widget/Loading";
import { BASE_MEDIA_URL } from "../authentication/api";
import { useFetchData } from "../widget/useFetchData";
import { useAuthContext } from '../authentication/useAuthContext';
import { LOGINPATH } from '../authentication/api';
import { Navigate } from 'react-router-dom';



const columns = [
  {
    name: "Name",
	selector: (row) => row.full_name,
	minWidth:"200px",
    sortable: true,
    cell: (row)=>{
		return (
			<div className="mt-3 mb-3">
				<div className="media align-items-center">
					<div className="media-head me-2">
						<div className="avatar avatar-xs avatar-rounded">
							<img src={`${BASE_MEDIA_URL}${row.avatar}`} alt={row.full_name} className="avatar-img" />
						</div>
					</div>
					<div className="media-body">
						<div className="text-high-em">{row.full_name}</div> 
						<p className="fs-7">{row.position}</p> 
					</div>
				</div>
			</div>
	  )
    }
  },
  {
		name: "Location",
		selector: (row) => row.location,
		sortable: true,
		cell: (row)=>{
			return (
				<div className="mt-3 mb-3">
					<div className="media align-items-center">
						<div className="media-body">
							<div className="text-high-em">{row.region}</div> 
							<p className="fs-7">{row.country}</p> 
						</div>
					</div>
				</div>
				)
			}
  },
  {
		name: "Client/Department",
		selector: (row) => row.client,
		sortable: true,
	    left: true,
		cell: (row) => (
			<p className="fs-7">{ row.client}</p>
		)
  },
  {
		name: "Status",
		selector: (row) => row.status,
		sortable: true,
	    right: true,
		cell: (row) => (
			<p className="fs-7">{ row.status}</p>
		)
	},
  {
		name: "Start Date",
		selector: (row) => row.start_date,
		sortable: true,
	    right: true,
		cell: (row) => (
			<p className="fs-7">{ row.start_date}</p>
		)
  },
];

export const Placement = () => {
	const URL = `agent/placement/get-placement-list/`
	const {error, isLoaded, data, fetchData } = useFetchData(URL)

	const handleRowSelected = useCallback(state => {
		// setSelectedRows(state.selectedRows);
	}, []);

	const contextActions = useMemo(() => {
		return (
			<>
				<button className="btn btn-primary btn-sm ms-1">
					Activate
				</button>
				<button className="btn btn-warning btn-sm ms-1">
					Deactivate
				</button>
				<button className="btn btn-danger btn-sm ms-1">
					Delete
				</button>
			</>
			
		);
	}, []);

	const actionsMemo = useMemo(() => {
		return (
			<>
				<input type="text" className="form-control" placeholder="Search Placement..." style={{width:"200px"}} />
				<Export onExport={() => downloadCSV(data, ["full_name", "position", "location", "client", "start_date"], "placement data")} />
				
			</>
		)
	}, [data])
    const { user } = useAuthContext();
    
    if (!user) {
        return <Navigate replace to={LOGINPATH} />;
    }
    return (
        <>
            <div className="simplebar-content" style={{padding: "20px"}}>
				<div className="container mt-md-3 mt-3">
					{
						error !== null ?
							<RetryAgain func={fetchData} error={error} />
							: !isLoaded?<LoadingCircle />:
						
						<>
							<div className="d-flex flex-md-nowrap flex-wrap align-items-center justify-content-between mb-2">
								<div>
									<h5>Placement</h5>
									<p>Candidates you have hired so far</p>
								</div>
							</div>
							<DataTableBase
								title={(title)=><h1>Sample Data</h1>}
								columns={columns}
								data={data}
								actions={actionsMemo}
								contextActions={contextActions}
								onSelectedRowsChange={handleRowSelected}
								// clearSelectedRows={toggleCleared}
								selectableRows
							/>
						</>
					}
                </div>
            </div>
        </>
    )
}