import { useMemo } from "react";
import DataTableBase from '../widget/dataTable';
import { downloadCSV, Export } from '../widget/exports';
import { Link } from "react-router-dom";
import { LoadingCircle } from "../widget/Loading";
import { RetryAgain } from "../widget/Loading";
import { useFetchData } from "../widget/useFetchData";
import { useAuthContext } from '../authentication/useAuthContext';
import { LOGINPATH } from '../authentication/api';
import { Navigate } from 'react-router-dom';




const columns = [
  {
    name: "Name",
    selector: (row) => row.company,
    sortable: true,
	minWidth:"250px",
    cell: (row)=>{
		return (
			<div className="mt-3 mb-3">
				<div className="media align-items-center">
					<div className="media-body">
						<div className="text-high-em">{row.company}</div> 
						<p className="fs-7">{row.client}</p> 
					</div>
				</div>
			</div>
	  )
    }
  },
  {
    name: "Contact",
    selector: (row) => row.email,
	sortable: true,
    cell: (row)=>{
		return (
			<div className="mt-3 mb-3">
				<div className="media align-items-center">
					<div className="media-body">
						<div className="text-high-em">{row.phone}</div> 
						<p className="fs-7">{row.email}</p> 
					</div>
				</div>
			</div>
	  )
    }
  },
  {
		name: "Job Count",
		selector: (row) => row.job_count,
		sortable: true,
		center: true,
		cell: (row) => (
			<p className="fs-7">{ row.job_count}</p>
		)
  },
  {
		name: "Industry",
		selector: (row) => row.industry,
		sortable: true,
	    left: true,
		cell: (row) => (
			<p className="fs-7">{ row.industry}</p>
		)
	},
  {
    name: "Address",
    selector: (row) => row.country,
    sortable: true,
	left: true,
    cell: (row)=>{
		return (
			<div className="mt-3 mb-3">
				<div className="media align-items-center">
					<div className="media-body">
						<div className="text-high-em">{row.country}</div> 
						<p className="fs-7">{row.city}</p> 
					</div>
				</div>
			</div>
	  )
    }
	},
	{
		button: true,
		name: "Actions",
		cell: () => (
				<div className="card-action-wrap">
					<button className="btn btn-icon btn-rounded btn-flush-dark flush-soft-hover dropdown-toggle no-caret" href="#" data-bs-toggle="dropdown"><span className="icon"><span className="feather-icon"><svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" className="feather feather-more-vertical"><circle cx="12" cy="12" r="1"></circle><circle cx="12" cy="5" r="1"></circle><circle cx="12" cy="19" r="1"></circle></svg></span></span></button>
					<div className="dropdown-menu dropdown-menu-end">
						<Link className="dropdown-item">Action</Link>
						<Link className="dropdown-item">Another action</Link>
						<Link className="dropdown-item">Something else here</Link>
						<div className="dropdown-divider"></div>
						<Link className="dropdown-item">Separated link</Link>
					</div>
				</div>
	)
  }
];

export const Client = () => {
	const URL = `agent/clients/get-client-list/`
	const {error, isLoaded, data, fetchData} = useFetchData(URL)

	const actionsMemo = useMemo(() => {
		return (
			<>
				<input type="text" className="form-control" placeholder="Search Placement..." style={{width:"200px"}} />
				<Export onExport={() => downloadCSV(data, ["full_name", "position", "location", "client", "start_date"], "placement data")} />
				<Link className="btn btn-secondary" to="/company/clients/add-client">Add Client</Link>
			</>
		)
	}, [data])
    const { user } = useAuthContext();
    
    if (!user) {
        return <Navigate replace to={LOGINPATH} />;
    }
    return (
        <>
            <div className="simplebar-content" style={{padding: "20px"}}>
				<div className="container mt-md-3 mt-3">
					{
						error !== null ?
							<RetryAgain func={fetchData} error={error} />
							: !isLoaded?<LoadingCircle />:
						<>
							<div className="d-flex flex-md-nowrap flex-wrap align-items-center justify-content-between mb-2">
								<div>
									<h5>Department/Client</h5>
									<p>Manage and track your clients/department efficiently</p>
								</div>
							</div>
							<DataTableBase
								title={(title)=><h1>Sample Data</h1>}
								columns={columns}
								data={data}
								actions={actionsMemo}
							/>
						</>
					}
                </div>
            </div>
        </>
    )
}
